html {
  box-sizing: border-box;
  scroll-behavior: smooth;
  height: 100%; }

body {
  font-family: "Gilroy", "Arial", sans-serif;
  font-style: normal;
  margin: 0;
  color: #141414; }

*,
*::before,
*::after {
  box-sizing: inherit; }

img {
  max-width: 100%;
  height: auto; }

a {
  text-decoration: none; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

@font-face {
  font-family: 'Gilroy';
  src: local("Gilroy Bold"), local("Gilroy-Bold"), url("../fonts/Gilroy-Bold.woff2") format("woff2"), url("../fonts/Gilroy-Bold.woff") format("woff");
  font-weight: bold;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: 'Gilroy';
  src: local("Gilroy Semibold"), local("Gilroy-Semibold"), url("../fonts/Gilroy-Semibold.woff2") format("woff2"), url("../fonts/Gilroy-Semibold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: 'Gilroy';
  src: local("Gilroy Medium"), local("Gilroy-Medium"), url("../fonts/Gilroy-Medium.woff2") format("woff2"), url("../fonts/Gilroy-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: 'Gilroy';
  src: local("Gilroy Extrabold"), local("Gilroy-Extrabold"), url("../fonts/Gilroy-Extrabold.woff2") format("woff2"), url("../fonts/Gilroy-Extrabold.woff") format("woff");
  font-weight: 800;
  font-style: normal;
  font-display: swap; }

.page-header {
  max-width: 1200px;
  margin: 0 auto; }

.page-header__horizontal-menu {
  display: flex;
  justify-content: space-between;
  margin-bottom: 78px; }

.page-header__wrapper {
  display: flex;
  flex-direction: column;
  padding: 0 20px; }

.page-header__wrapper--bottom {
  display: flex;
  justify-content: space-between; }

.be-in-touch {
  flex-basis: 320px;
  padding: 15px 18px;
  display: flex;
  justify-content: space-between;
  align-items: center; }

.be-in-touch__link {
  height: 46px;
  width: 200px;
  border: 1px solid rgba(75, 1, 224, 0.34);
  border-radius: 23px;
  display: flex;
  justify-content: center;
  align-items: center; }
  .be-in-touch__link:hover {
    background-color: #4B01E0; }
    .be-in-touch__link:hover svg {
      fill: #FFFFFF; }
  .be-in-touch__link svg {
    fill: #141414; }

.main-nav {
  padding: 28px 18px;
  max-width: 744px;
  flex-grow: 1; }

.main-nav__list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  justify-content: space-between; }

.main-nav__link {
  font-size: 18px;
  font-weight: 500;
  letter-spacing: -0.02em;
  color: #141414;
  position: relative; }
  .main-nav__link:hover:before {
    content: "";
    position: absolute;
    left: 0;
    bottom: -9px;
    width: 100%;
    height: 3px;
    background-color: #4B01E0; }

.introduce {
  display: flex;
  justify-content: space-between;
  margin-bottom: 47px; }

.introduce__text {
  padding-top: 62px; }

.introduce__title {
  font-size: 50px;
  letter-spacing: -0.027em;
  line-height: 60px;
  font-weight: bold;
  margin: 0 0 14px 0; }

.introduce__description {
  font-size: 24px;
  letter-spacing: -0.02em;
  font-weight: 500;
  margin: 0 0 33px 0; }

.introduce__buttons {
  display: flex; }

.button {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #141414; }

.button--introduce {
  height: 65px;
  flex-basis: 260px;
  border-radius: 10px; }

.button--contact-me {
  background-color: #EFCA33;
  margin-right: 10px; }
  .button--contact-me:hover, .button--contact-me:focus {
    background-color: #ffdc4b; }

.button--introduce.button--what-i-can {
  border: 1px solid rgba(75, 1, 224, 0.34);
  flex-basis: 210px; }
  .button--introduce.button--what-i-can:hover, .button--introduce.button--what-i-can:focus {
    background-color: #4B01E0;
    color: #FFFFFF; }

.button--resume {
  border: 1px solid rgba(75, 1, 224, 0.34);
  flex-basis: 214px;
  height: 56px;
  border-radius: 10px; }
  .button--resume:hover, .button--resume:focus {
    background-color: #4B01E0;
    color: #FFFFFF; }
